.friendMessage {
    align-self: flex-end;
    width: 575px;
    height: 77px;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    max-width: 100%;



}

.friendImageAndText {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
}

.friendText {
    margin-left: 27px;
    display: flex;
    flex-direction: column;
    color: #FFF;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

}




.friendName {
color: black;

    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.friendMessageText {
    color: black;

    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.friendTime {

    color: #000;
    font-family: Montserrat;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-left: 10px;
}
