.message {
max-width: 100%;
    width: 575px;
    height: 77px;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;


}

.imageAndText {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {
    margin-left: 27px;
    display: flex;
    flex-direction: column;


}



.name {
    margin-bottom: 4px;
    color: #000;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.messageText {

    margin:0;
    padding: 0;
    color: #000;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.time {

    padding: 0;
    color: #000;
    font-family: Montserrat;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-left: 10px;
    max-width: 100%;
}